.box_header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    background-color: transparent;
    height: 60px;
}

.optionsOnHeader {
    display: -ms-flexbox;
    display: flex;
    background-color: transparent;
    height: 100%;
    width: 55%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 40px;
}

.options {
    text-decoration: none;
    color: var(--titleWhite);
    font-size: 1.2em;
    font-family: 'Oswald', sans-serif;
}

.options:hover {
    border-bottom: 1px solid whitesmoke;
    font-weight: bold;
    cursor: pointer;
}


